import React, { useState, useEffect } from 'react';
import Card from './Card';
import audi_s7 from '../images/audi_s7.jpg';
import audi_a7 from '../images/audi_a7.jpg';
import BenzAMG from '../images/BenzAMG.jpeg';
import Audi_R8 from '../images/Audi_R8.jpg';
import BMW_cadillac_ct5 from '../images/BMW_cadillac_ct5.jpg';
import dodge_challenger from '../images/dodge_challenger.jpg';
import Ford_Mustang_EcoBoost from '../images/Ford_Mustang_EcoBoost.jpg';
import koenigsegg_gemera from '../images/koenigsegg_gemera.jpg';
import Mercedes_EQS from '../images/Mercedes_EQS.jpg';
import Range_Rover_Evoque from '../images/Range_Rover_Evoque.jpg';
import toyota_86 from '../images/toyota_86.jpg';
import toyota_supra from '../images/toyota_supra.jpg';

const Cards = (props) => {
    const { handleGame, score , highScore } = props;

    let images = [
        {
            name: BenzAMG,
            title: 'Benz AMG',
        },
        {
            name: audi_a7,
            title: 'Audi A7',
        },
        {
            name: BMW_cadillac_ct5,
            title: 'BMW Cadillac CT5',
        },
        {
            name: dodge_challenger,
            title: 'Dodge Challenger',
        },
        {
            name: Audi_R8,
            title: 'Audi R8',
        },
        {
            name:  Ford_Mustang_EcoBoost,
            title: 'Ford Mustang',
        },
        {
            name: koenigsegg_gemera,
            title: 'Koenigsegg Gemera',
        },
        {
            name: Mercedes_EQS,
            title: 'Mercedes EQS',
        },
        {
            name: Range_Rover_Evoque,
            title: 'Range Evoque',
        },
        {
            name:  toyota_86,
            title: 'Toyota 86',
        },
        {
            name: toyota_supra,
            title: 'Toyota Supra',
        },
    ]

    const [cards, setCards] = useState(images);

    const shuffle = (myCards) => {
        for ( let i = 0; i < myCards.length; i++ ){
            let randomCard = Math.floor(Math.random() * i);
            [myCards[randomCard], myCards[i]] = [myCards[i], myCards[randomCard]];
        }
    }

    useEffect( () => {
        const myCards = [...cards];
        shuffle(myCards);
        setCards(myCards);
    }, [score, highScore]);

    return (
        <>
        {cards.map( (card) => (
            <Card card={card} key={card.title} handleGame={handleGame}/>
        ))
        }
        </>
    );
};

export default Cards;



