import React, { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.css';

const Card = (props) => {
    const {
        card: {name,title}, handleGame} = props;

    return(
        <button onClick={handleGame.bind(this, title)} style={{border: 'none', marginBottom: '10px', marginTop: '10px'}}>
            <div className='card' style={{width: "30rem",height: "20rem", border: 'none' } }>
                <img width='100' className='card-img-top' src={name}></img>
            </div>
            <div className='card-title'>
                <h5>{title}</h5>
            </div>
        </button>
    )
}

export default Card;