import React, { useState, useEffect } from 'react';
import Cards from './Cards';
import Nav from './Nav';
import theRoad from '../images/theRoad.jpg';

const Play = () => {
    const [score, setScore] = useState(0);
    const [highScore, setHighScore] = useState(0);
    const [imagesArray, setImagesArray] = useState([]);

    const handleScore = () => {
        setScore((lastScore) => lastScore + 1);
    }
    const handleHighScore = () => {
        setHighScore(score);
    }
    const handleImage = (name) => {
        setImagesArray( (lastImage) => [...lastImage, name]);
    };

    const reset = ()=> {
        setScore(0);
        setImagesArray([]);
    }
    const handleGame = (name) => {
        if(!imagesArray.includes(name)){
            handleImage(name);
            handleScore();
        }else{
            handleHighScore();
            reset();
        }
    };

    return(
        <>
        <Nav score={score} highScore={highScore} />
        <div style={{backgroundImage: `url(${theRoad})`, backgroundPosition:"center", backgroundRepeat: "no-repeat", backgroundSize: "cover",
                      display: 'flex', justifyContent: 'center'}}>
            <div style={{
            display: "flex", flexDirection: "column", justifyItems: 'center'}}>
                <Cards handleGame = {handleGame} score={score} highScore={highScore} />
            </div>
        </div>
        
        </>
    );
};

export default Play;